.Login .Inputs{
  padding: 10px;
  border: none;
}

.Login{
  width:100%;
  font-size:1.8em;
  font-family: 'Shrikhand', cursive;
  height:95vh;
  color:white;
  display:flex;
  justify-content: center;
  align-items: center
}

.Login .Link {
  color:white
}

.Login .FullScreen {
  position:absolute;
  left:0;
  top:5vh;
  z-index:-1;
  width:100%;
  height: 95vh;
  border:none
}

.Login .LoginContent {
  display:flex;
  justify-content: center;
  align-items: center;
  width:20%;
  background-color:rgba(0, 0, 0, 0.7) ;
  padding:2%
}

.Login .LoginContent  div {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top:2%;
}

.Login .LoginContent form {
  margin:0
}